@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {
  margin: 0;
  font-family: "Noto Sans KR" !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: url(/static/media/logo_bg.7536b383.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}

.Header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 5rem;
}

.header-left {
  display: inline-block;
  height: 5rem;
  width: auto;
  margin-left: 0.5rem;
}

.header-center {
  font-size: 3rem;
  color: white;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  line-height: 5rem;
}

.header-right {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: right;
  line-height: 5rem;
  margin-right: 1rem;
}

.Main {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  font-size: 5rem;
  font-weight: 500;
  line-height: 100vh;
}

.Footer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 5rem;
}

.footer-right {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: right;
  line-height: 5rem;
  margin-right: 1rem;
}

p {
  margin: 0;
}

